
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import WriteMail from './WriteMail'
import * as TimeUtil from './timeUtil'

export default {
props: {
showTitle: Boolean
},
directives: {
TransferDom
},
components: {
Scroller,
XDialog,
CheckIcon,
Msg,
Checker,
Datetime,
CheckerItem,
Cell,
WriteMail,
ViewBox,
XButton,
XHeader,
PopupHeader,
Popup,
Group,
LoadMore
},
data () {
return {
showPop: false,
showSelectTime: false,
sendMailOption: false,
currentUser: {},
currentPage: 'home',
pageNo: 0,
sendTime: {
time: '',
custom_time: '',
value: '0.5'
},
mailOptions: {
mailCount: 0,
receiverMail: '',
isPublic: true,
content: '',
isEncryption: false,
sendSuccess: false
},
modelShow: 'mailList',
styleObj: {
'z-index': 301,
'height': 'calc(100% - 46px - env(safe-area-inset-top))',
'top': 'calc(46px + env(safe-area-inset-top))'
},
sendOptionStyle: {
'z-index': 501,
'height': 'calc(100% - 46px - env(safe-area-inset-top))',
'top': 'calc(46px + env(safe-area-inset-top))'
},
dialogStyle: {
'max-width': '100%',
'width': '90%',
'height': '50%',
'background-color': 'transparent'
},
pullUpConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
pullDownConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
tempMailList: [],
publicMailList: []
}
},
mounted () {
this.getMailCount()
this.currentUser = this.$db.get('User')
this.sendTime.time = TimeUtil.getYear(0.5)
this.loadMailListByPage(0)
console.log(this.publicMailList)
this.$nextTick(() => {
this.$refs.demo1.reset({top: 0})
})
},
methods: {
loadMailListByPage (pageNo) {
this.$api.mailApi.getPublicMailList(pageNo).then(result => {
if (result.data.success) {
this.publicMailList = result.data.data
this.pageNo += 1
} else {
this.$vux.toast.show({
type: 'text',
text: ''
})
}
})
},
doSendMail () {
if (this.$utils.checkEmail(this.mailOptions.receiverMail)) {
console.log(':')
const sendDate = this.sendTime.custom_time === '' ? this.sendTime.time : this.sendTime.custom_time
if (TimeUtil.isBigThanToday(sendDate)) {

this.$api.mailApi.sendMail(this.mailOptions.receiverMail, this.mailOptions.content, sendDate, this.mailOptions.isPublic, this.currentUser.userId).then(result => {
if (result.data.success) {
this.mailOptions.sendSuccess = true
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
width: '12em',
text: '!'
})
}
} else {
this.$vux.toast.show({
type: 'text',
width: '12em',
text: '!'
})
}
},
timeChange (val) {
console.log(val)
if (val <= 10) {
this.sendTime.custom_time = ''
this.sendTime.time = TimeUtil.getYear(val)
} else {
const that = this
this.$nextTick(() => {
that.showSelectTime = true
})
}
},
setCustomTime () {
this.sendTime.value = 99
console.log(this.sendTime.custom_time)
},
closeDataView () {
this.sendTime.value = 99
},
changePublic () {
console.log('click public')
this.mailOptions.isPublic = !this.mailOptions.isPublic
},
setBodyTop () {
console.log(',')
document.body.scrollTop = 0
document.documentElement.scrollTop = 0

},
clickSendMail () {
this.sendMailOption = true
this.changeTitle('')
},
showTimeWindow () {
this.showSelectTime = true
this.sendTime.value = 99
},
vieMail () {
this.showPop = true
this.currentPage = 'mailList'
this.modelShow = 'mailList'
this.changeTitle('')




},
changeTitle (title) {
this.$emit('change-title', title)
},
writeMail () {

this.showPop = true
this.modelShow = 'sendMail'
this.changeTitle('')
},
closeWindow () {
this.changeTitle('')
this.currentPage = 'home'
this.showPop = false
this.sendMailOption = false
},
backHome () {
this.changeTitle('')
this.currentPage = 'home'
this.showPop = false
},
replaceStr (str) {
return str.replace(str.substring(0, 4), '****')
},
openAbout () {
this.changeTitle('')
this.showPop = false
this.currentPage = 'about'
this.$emit('close-about')
},
pullUpLoad () {
this.$api.mailApi.getPublicMailList(this.pageNo).then(result => {
if (result.data.success) {
this.$refs.demo1.donePullup()
this.tempMailList.length = 0
this.tempMailList = result.data.data
if (this.tempMailList.length > 0) {
this.pageNo += 1
this.publicMailList = this.publicMailList.concat(this.tempMailList)
}
} else {
this.$vux.toast.show({
type: 'text',
text: ''
})
}
})
},
getMailCount () {
this.$api.mailApi.getMailCount().then(result => {
if (result.data.success) {
this.mailOptions.mailCount = result.data.data
}
})
},
pullDownLoad () {
this.$api.mailApi.getPublicMailList(this.pageNo).then(result => {
if (result.data.success) {
this.$refs.demo1.donePulldown()
this.tempMailList.length = 0
this.tempMailList = result.data.data
if (this.tempMailList.length > 0) {
this.pageNo += 1
this.publicMailList = this.tempMailList.concat(this.publicMailList)
}
} else {
this.$vux.toast.show({
type: 'text',
text: ''
})
}
})
}
}
}



